@import url(./reset.css);

main{
  color: #BA9653;
  height: 100px;
}

.flexbox{
  display: flex;
}

.header{
  background-color: rgba(21, 28, 32, 0.567);
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: solid 2px #BA9653;
  border-top: solid 2px #BA9653;
  justify-content: space-between;
}

.header nav{
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.footer{
  position: relative;
  width: 100%;
  margin-top: 3rem;
  background-color: rgba(21, 28, 32, 0.567);
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: solid 2px #BA9653;
  border-top: solid 2px #BA9653;
  text-align: center;
  bottom: 0px;
}

.footer .logo {
  font-size: 1.5rem;
}

body{
  background-color: #063d3a;
}

.logo{
  text-decoration: none;
  font-size: 2rem;
  color: #BA9653;
}

a{
  text-decoration: none;
}

.add-link{
  text-decoration: none;
  color: white;
}
.no-link{
  text-decoration: none;
  color: white;
}

.nav-btns{
  align-self: center;
  padding-left: 1rem;
  justify-content: space-between;
}

button{
  height: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #BA9653;
  color: rgb(255, 255, 255);
  font-weight: 700;
  border: solid #BA9653;
  border-radius: 3px;
  margin-left: 0.5rem;
}

button:hover{
  background-color: #ba965300;
  border: solid #BA9653;
}

.catalog{
  text-align: center;
  width: 80%;
  padding-top: 3rem;
  margin-left: auto;
  margin-right: auto;
}

.catalog-title{
  text-align: center;
  padding-bottom: 1rem;
}

.cat-item{
  background-color:#053431;
  border-radius: 5px;
  padding-bottom: 0.5rem;
}

.cat-img{
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0.5rem;
  border-radius: 5px;
}

.item-link{
  text-decoration: none;
  text-transform: capitalize;
  color: #BA9653;
}

.grid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 1rem;
}

.sort-button{
  position: relative;
  text-align: center;
  margin-left: 0;
  margin-bottom: 1rem;
}

.current-sort{
  height: 2rem;
  width: 3.5rem;
  border-radius:3px;
  text-align: center;
}

.details-page{
  height: 100%;
}
.details{
  margin-left: auto;
  margin-right: auto;
  margin-top: 3rem;
  width: 80%;
  height: 100%;
}

.object-text{
  width: 50%;
  padding-left: 1rem;
  align-items: baseline;
}
.object-title {
  padding-bottom: 1rem;
  text-align: center;
  text-transform: capitalize;
}
.object-text p{
  padding-bottom: 0.5rem;
}
.object-desc {
  justify-content: center;
}
.object-image {
  border-radius: 1rem;
}

.error {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 40%;
  text-align: center;
}

.error h1{
  font-size:5rem;
}

.error h4{
  margin-bottom: 1rem;
}

.home-btn{
  color:#ffffff;
}